<template lang="pug">
    v-card 
        v-card-text
            v-card-title
                span Odk编码
            v-container
                v-row 
                    v-col(cols="12" sm="12" lg="12")
                        v-text-field(outlined hide-details v-model="input"  label="输入")
                    v-col(cols="12" sm="12" lg="12")
                        v-text-field(outlined hide-details v-model="output"  label="输出")
                    v-col(cols="12" sm="12" lg="12")
                        v-combobox(
                            v-model="type"
                            :items="types"
                            label="编码类型"
                        )
                    
            v-card-actions
                v-btn(color="success" dark @click="encode") 编码
</template>

<script>
import * as api from "@/apis/wswebapi"
export default {
    data: () => ({
    input: "",
    output: "",
    types:[{text:"ODKENCODE",value:"ODKENCODE"},
    {text:"ODKENCRYPTE",value:"ODKENCRYPTE"},
    ],
    type:{text:"ODKENCRYPTE",value:"ODKENCRYPTE"},
    }),
    methods:{
        encode(){
            if (this.type.value=="ODKENCODE"){
                api.OdkEncode({text:this.input}).then(res=>{
          
                    if (res.code==200){
                        this.output=res.data;
                        this.$store.dispatch('msglog/push_msg',{message:res.msg,type:"success"})
                    }else{
                        this.$store.dispatch('msglog/push_msg',{message:res.msg+res.error,type:"error"})
                    }
                })
            }else if (this.type.value=="ODKENCRYPTE"){
                api.OdkEncrypteString({text:this.input}).then(res=>{
              
                    if (res.code==200){
                        this.output=res.data;
                        this.$store.dispatch('msglog/push_msg',{message:res.msg,type:"success"})
                    }else{
                        this.$store.dispatch('msglog/push_msg',{message:res.msg+res.error,type:"error"})
                    }
                })

            }
            
            
        }
    }
}
</script>